import NProgress from 'nprogress'; // progress bar

import 'nprogress/nprogress.css'; // progress bar style

import { GetUtcTime } from '@/api/equipmentManagement/status'; // 数据接口

export default {
  name: 'DataChoose',
  data: function data() {
    return {
      visible1: false,
      visible2: false,
      quickSelect: [{
        value: 5,
        label: 'minute',
        unit: this.$t('view.minute')
      }, {
        value: 30,
        label: 'minute',
        unit: this.$t('view.minute')
      }, {
        value: 1,
        label: 'hour',
        unit: this.$t('view.hour')
      }, {
        value: 6,
        label: 'hour',
        unit: this.$t('view.hour')
      }, {
        value: 1,
        label: 'day',
        unit: this.$t('view.day')
      }],
      pickerOptions2: {
        shortcuts: [{
          text: this.$t('view.lastWeek'),
          onClick: function onClick(picker) {
            var end = new Date();
            var start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: this.$t('view.lastMonth'),
          onClick: function onClick(picker) {
            var end = new Date();
            var start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: this.$t('view.threeMonth'),
          onClick: function onClick(picker) {
            var end = new Date();
            var start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
            picker.$emit('pick', [start, end]);
          }
        }]
      },
      selectValue: 1,
      selectUnit: 'minute',
      timeSlot: [{
        value: 'second',
        label: this.$t('view.second')
      }, {
        value: 'minute',
        label: this.$t('view.minute')
      }, {
        value: 'hour',
        label: this.$t('view.hour')
      }, {
        value: 'day',
        label: this.$t('view.day')
      }],
      timeRange: '',
      timeOut: '',
      titVal: '',
      dataVal: this.$t('view.dataQuery')
    };
  },
  watch: {
    dataVal: function dataVal() {
      this.$emit('choose-val', this.dataVal);
    }
  },
  destroyed: function destroyed() {
    if (this.timeOut) {
      clearInterval(this.timeOut);
    }
  },
  methods: {
    quick: function quick(e) {
      this.selectUnit = e.label;
      this.selectValue = e.value;
      this.searchReal();
    },
    searchHistory: function searchHistory() {
      NProgress.start();

      if (this.timeOut) {
        clearInterval(this.timeOut);
      } // 历史数据搜索


      this.$parent.startDate = this.timeRange[0];
      this.$parent.endDate = this.timeRange[1];
      this.$parent.deviceId = this.$route.params.id;
      this.$parent.startTime = this.$moment(this.$parent.startDate).utc().local().format('YYYY/MM/DD HH:mm:ss');
      this.$parent.endTime = this.$moment(this.$parent.endDate).utc().local().format('YYYY/MM/DD HH:mm:ss');
      this.$parent.getDevice(this.$parent.currentTemplate);
      this.visible2 = false;
      this.dataVal = this.$t('view.historyData');
    },
    searchReal: function searchReal() {
      var _this = this;

      //   console.log(this.$parent.currentTemplate, 'this.$parent.currentTemplate')
      NProgress.start();

      if (this.timeOut) {
        clearInterval(this.timeOut);
      }

      this.timeOut = ''; // 实时数据搜索

      if (this.$parent.endTime) {
        this.$parent.endTime = '';
      }

      if (this.selectUnit === 'second') {
        this.titVal = this.$t('view.second');
      } else if (this.selectUnit === 'minute') {
        this.titVal = this.$t('view.minute');
      } else if (this.selectUnit === 'hour') {
        this.titVal = this.$t('view.hour');
      } else if (this.selectUnit === 'day') {
        this.titVal = this.$t('view.day');
      }

      this.dataVal = this.selectValue + ' ' + this.titVal + '  --  ' + this.$t('view.RealDataUpdates'); // 标题展示

      if (this.selectUnit === 'second') {
        this.selectValue = this.selectValue * 1000;
      } else if (this.selectUnit === 'minute') {
        this.selectValue = this.selectValue * 60 * 1000;
      } else if (this.selectUnit === 'hour') {
        this.selectValue = this.selectValue * 60 * 60 * 1000;
      } else if (this.selectUnit === 'day') {
        this.selectValue = this.selectValue * 24 * 60 * 60 * 1000;
      }

      var tempSelectValue = this.selectValue; // 选择时间的毫秒值

      var reqTime = '';
      GetUtcTime().then(function (res) {
        reqTime = res.data.utcTime.replace(/-/g, '/'); // replace 为解决safari游览器, 时间兼容问题

        _this.$parent.startTime = _this.$moment(new Date(new Date(reqTime).getTime() - tempSelectValue)).local().format('YYYY/MM/DD HH:mm:ss'); // 开始的时间
        // console.log(tempSelectValue, 'tempSelectValue')
      });
      this.$parent.getDevice(this.$parent.currentTemplate);
      this.timeOut = setInterval(function () {
        GetUtcTime().then(function (res) {
          reqTime = res.data.utcTime.replace(/-/g, '/'); // replace 为解决safari游览器, 时间兼容问题

          _this.$parent.startTime = _this.$moment(new Date(new Date(reqTime).getTime() - tempSelectValue)).local().format('YYYY/MM/DD HH:mm:ss'); // 开始的时间

          _this.endTime = _this.$moment(reqTime).utc().local().format('YYYY/MM/DD HH:mm:ss'); // 结束的时间
        });

        _this.$parent.getDevice(_this.$parent.currentTemplate);
      }, 5000);

      if (this.selectUnit === 'second') {
        this.selectValue = this.selectValue / 1000;
      } else if (this.selectUnit === 'minute') {
        this.selectValue = this.selectValue / 60 / 1000;
      } else if (this.selectUnit === 'hour') {
        this.selectValue = this.selectValue / 60 / 60 / 1000;
      } else if (this.selectUnit === 'day') {
        this.selectValue = this.selectValue / 24 / 60 / 60 / 1000;
      }

      this.visible1 = false;
    }
  }
};