import Template from '@/components/template';
import DataChoose from "./dataChoose";
import { GetData } from '@/api/devicelist'; // getDeviceData,

import { GetTemplateInfoByDeviceId } from '@/api/equipmentData/dataView'; // 模板接口

import { GetById, GetUtcTime } from '@/api/equipmentManagement/status'; // 数据接口

import NProgress from 'nprogress'; // progress bar

import 'nprogress/nprogress.css'; // progress bar style

import { buttonPermissions } from '@/mixin';
export default {
  name: 'Deviceview',
  components: {
    Template: Template,
    DataChoose: DataChoose
  },
  mixins: [buttonPermissions],
  data: function data() {
    return {
      startDate: {},
      endDate: {},
      startTime: '',
      pageIndex: 1,
      pageSize: 200,
      data: {},
      templates: [],
      activeName: '',
      currentTemplate: {},
      name: name,
      value: '',
      href: '',
      viewName: ''
    };
  },
  computed: {
    zdpagesize: function zdpagesize() {
      return this.$store.state.tableView.pageSize;
    },
    zdpageindex: function zdpageindex() {
      return this.$store.state.tableView.pageIndex;
    } // ZDfields() {
    //   return this.$store.state.tableView.fields
    // }

  },
  watch: {
    zdpagesize: function zdpagesize() {
      this.getDevice(this.currentTemplate);
    },
    zdpageindex: function zdpageindex() {
      this.getDevice(this.currentTemplate);
    },
    ZDfields: function ZDfields() {
      this.getDevice(this.currentTemplate);
    }
  },
  created: function created() {
    var now = new Date();
    this.startDate = new Date(Date.UTC(now.getFullYear(), now.getMonth(), now.getDate() - 1)).toISOString().slice(0, 10);
    this.endDate = new Date();
    this.deviceId = this.$route.params.id;
    this.timeRange = [this.startDate, this.endDate];
  },
  mounted: function mounted() {
    var _this = this;

    GetTemplateInfoByDeviceId(this.$route.params.id).then(function (res) {
      var templates = res.data.filter(function (item) {
        if (item.viewTypeId === 100) {
          return item;
        }
      });

      if (templates.length > 0) {
        _this.$set(_this, 'templates', templates);

        _this.activeName = _this.templates[0].viewId.toString();

        _this.templates.forEach(function (item) {
          return item.viewId = item.viewId.toString();
        });

        _this.currentTemplate = _this.templates[0]; // this.startTime = this.$moment(this.startDate).utc().local().format('YYYY/MM/DD HH:mm:ss')

        var reqTime = '';
        GetUtcTime().then(function (res) {
          reqTime = res.data.utcTime.replace(/-/g, '/'); // replace 为解决safari游览器, 时间兼容问题

          _this.startTime = _this.$moment(new Date(new Date(reqTime).getTime() - 1800000)).local().format('YYYY/MM/DD HH:mm:ss'); // 开始的时间 

          _this.$refs.chooseData.selectValue = 30;
          _this.$refs.chooseData.selectUnit = 'minute';

          _this.$refs.chooseData.searchReal();
        });
      }
    }); // 获取设备信息

    GetById(this.$route.params.id).then(function (res) {
      _this.name = res.data.name;
    });
  },
  methods: {
    derivedData: function derivedData() {
      var time = this.$moment(new Date()).utc().local().format();

      if (this.endTime !== undefined) {
        time = this.endTime;
      }

      time = encodeURIComponent(time);
      var startTime = encodeURIComponent(this.$moment(this.startTime).utc().local().format());

      if (this.data.items == undefined) {
        this.href = "javascript:;";
        this.$message({
          message: '空数据不能导出',
          type: 'warning'
        });
      } else {
        this.href = '/api/dms/DeviceData/ExportExcel?deviceId=' + this.deviceId + '&startTime=' + startTime + '&endTime=' + time + '&viewId=' + parseInt(this.activeName) + '&pageIndex=' + this.pageIndex + '&pageSize=' + this.pageSize;
      }
    },
    chooseVal: function chooseVal(dataVal) {
      this.value = dataVal;
    },
    handleSizeChange: function handleSizeChange(val) {
      NProgress.start();
      this.$store.commit('SETSIZE', val);
      this.pageSize = val;
    },
    handleCurrentChange: function handleCurrentChange(val) {
      NProgress.start();
      this.$store.commit('SETINDEX', val);
      this.pageIndex = val;
    },
    tabChanged: function tabChanged(tab) {
      this.viewName = tab.label;
      NProgress.start();
    },
    getDevice: function getDevice() {
      var _this2 = this;

      GetData({
        deviceId: this.deviceId,
        viewId: parseInt(this.activeName),
        startTime: this.startTime,
        endTime: this.endTime,
        pageIndex: this.pageIndex,
        pageSize: this.pageSize
      }).then(function (res) {
        _this2.data = res.data;
        NProgress.done();
      });
    }
  }
};